import _ from '../../services/utils'

class Popup {
  constructor(DOM = window && window.document) {
    this._DOM = DOM;
  }

  draw(parentElement) {
    this._parentElement = parentElement;
    this._render(parentElement);
  }

  /**
   * @param {Object} info
   *  @key {String} name
   *  @key {Number} value
   *  @key {String} color
   */
  show(x, y, title, info) {
    this._element.style.display = 'block';
    this._element.style.top = `${x}px`;
    while (this._info.childNodes.length != 0) {
        this._info.childNodes[0].remove();
    }

    this._title.innerHTML = title;
    _.forIn(info, row => {
      let node = this._createRow(row.name, row.value, row.color);
      this._info.appendChild(node);
    })
  }

  hide() {
    this._element.style.display = 'none';
  }

  _createRow(text, value, color) {
    let element = this._DOM.createElement('div')
    element.setAttribute('class', 'row');
    element.style.color = color;
    let prefix = this._DOM.createElement('span')
    prefix.innerHTML = text;
    element.appendChild(prefix);

    let v = this._DOM.createElement('span');
    v.innerHTML = value;
    element.appendChild(v)
    return element;
  }

  _render(parentElement) {
    let docFrag = this._DOM.createDocumentFragment();
    let element = this._DOM.createElement('div');
    element.setAttribute('class', 'popup');

    docFrag.appendChild(element);
    let title = this._DOM.createElement('span')
    title.setAttribute('class', 'title');
    this._title = title;
    element.appendChild(title);

    let info = this._DOM.createElement('div');
    this._info = info;
    element.appendChild(info);

    this._element = element;
    parentElement.appendChild(element);
  }
}

export default Popup;
