import _ from '../../services/utils'

class Select {
  constructor(DOM = window && window.document) {
    this._DOM = DOM;
  }

  draw(parentElement) {
    this._parentElement = parentElement;

    let docFrag = this._DOM.createDocumentFragment();
    let element = this._DOM.createElement('div');
    element.setAttribute('class', 'select');
    parentElement.appendChild(element);
    this._element = element;
  }

  clean() {
    while (this._element.childNodes.length !== 0) {
      this._element.childNodes[0].remove();
    }
  }

  /**
   * @param {Object} data
   *  @key {String} id
   *  @key {String} name
   *  @key {String} color
   */
  add(data, cb) {
    let element = this._createRow(data.id, data.name, data.color);
    this._element.appendChild(element);
    element.querySelector('input[type="checkbox"]').addEventListener('click', (e) => {
      let checked = e.target.checked;
      cb && cb(checked);
    });
  }

  _createRow(id, title, color) {
    let element = this._DOM.createElement('div')
    element.setAttribute('class', 'row');
    element.style.color = color;

    let checkbox = this._DOM.createElement('input');
    checkbox.setAttribute('type', 'checkbox');
    checkbox.setAttribute('checked', true);
    checkbox.setAttribute('id', id);
    element.appendChild(checkbox);

    let label = this._DOM.createElement('label');
    label.setAttribute('for', id);
    label.innerHTML = title;
    element.appendChild(label);
    return element;
  }

}

export default Select;
