import BaseSVG from './base'

class Title extends BaseSVG {
  static id = 0;
  static LOG_LEVEL = 0;
  static tagName = 'title';

  constructor(DOM = window && window.document) {
    super(DOM);
  }

  /**
   * @param {DOMElement} parentElement
   * @param {Object} opts
   *  @key {String} id
   */
  draw(parentElement, opts = {}, config = {}) {
    this.__draw(parentElement, opts, config);

    if (config.id !== undefined)
      this.attr({ id: config.id });

    return this;
  }

  text(value) {
    this.element.innerHTML = value;
  }
}

export default Title;
