import Logger from '../../services/Logger'
import _ from '../../services/utils'

import Grid from './Grid'
import Popup from './Popup'
import Select from './Select'

import { Circle, Defs, G, Path } from '../../svg/index'

import './styles.css'

const DEFAULT_WIDTH = 500;
const DEFAULT_HEIGHT = 300;
const yPadding = 20;

class Plot {
  static id = 0
  static LOG_LEVEL = 0

  config = {
    scaleX: 1,
    scaleY: 1
  }

  constructor(svg) {
    Plot.id++;
    this.console = new Logger(`Plot #${Plot.id}`, Plot.LOG_LEVEL);

    this._svg = svg;
    this._svg.className('plot')
    this._data = {}

    this._grid = new Grid(svg);
    this._popup = new Popup();
    this._popup.draw(this._svg.element.parentNode);
    this._select = new Select();
    this._select.draw(this._svg.element.parentNode);
  }

  /**
   * @param {Object} opts
   *  @key {String} width
   *  @key {String} height
   * @param {Object} config
   *  @key {Number} positionX (default = 0)
   *  @key {Number} positionY (default = 0)
   *  @key {Number} scaleX (default = 1)
   *  @key {Number} scaleY (default = 1)
   */
  draw(opts = {}, config) {
    const doc = this._svg.element.ownerDocument;
    const DOM = doc.createDocumentFragment();

    const padding = 10;
    let width = opts.width || DEFAULT_WIDTH;
    let height = opts.height || DEFAULT_HEIGHT;
  }

  clean() {
    this._data = {};
    this._grid.clean();
    this._graph && this._graph.clean();
    this._select.clean();
  }

  enable(id) {
    let data = this._data[id];
    if (!data)
      return;

    if (data.enabled)
      return;

    this._drawData(id);
  }

  disable(id) {
    let data = this._data[id];
    if (!data)
      return;

    if (!data.enabled)
      return;

    data.path.element.remove();
    data.path = null;
    data.enabled = false;
  }
  /**
   * @param {components/Preview} preview
   */
  linkWith(preview) {
    preview.subscribe('onScale', (data, [x1, x2], [xRatio, yRatio]) => {
      let scaledData = this._data[data.id].scaledData;
      let graphData = this._data[data.id].data;

      let localX1 = (x1 / xRatio) * scaledData.xRatio;
      let width = this._svg.element.getBoundingClientRect().width;
      let scale = _.ceil((width / scaledData.xRatio) / ((x2 - x1) / xRatio), 3)
      if (scale == this._graphScale)
        return;

      this._graphTranslate = -localX1 * scale;
      this._graphScale = scale;
      //TODO:
      this._graphX1 = x1;
      this._graph.attr({
        transform: `translate(${this._graphTranslate} 0) scale(${this._graphScale} 1)`
      })
    })

    preview.subscribe('onChange', (data, [x1, x2], [xRatio, yRatio]) => {
      let scaledData = this._data[data.id].scaledData;
      let graphData = this._data[data.id].data;

      let localX1 = (x1 / xRatio) * scaledData.xRatio;
      this._graphTranslate = -localX1 * this._graphScale;
      //TODO:
      this._graphX1 = x1;
      this._graph.attr({
        transform: `translate(${this._graphTranslate} 0) scale(${this._graphScale} 1)`
      })
    })


    preview.subscribe('onDraw', this._onDrawData.bind(this, preview));
    this._bindUI();
  }

  /**
   * All datas MUST have the same dimensions!
   */
  _onDrawData(preview, data, [x1, x2], [xRatio, yRatio], proportion) {
    this._data[data.id] = {data: data, proportion: proportion, xRatio: xRatio};

    this._grid.updateTitles(null, null, data.maxYValue);

    if (!this._graph) {
      this._graph = new G();
      this._graph
        .draw(this._svg.element)
    }

    let height = this._getPlotHeight();
    let width = height * proportion

    let scaledData = data.scaleTo(width, height);
    this._data[data.id].scaledData = scaledData;
    if (scaledData.proportion !== proportion) {
      this.console.error(`Something wrong with proprotions: ${scaledData.proportion} and ${proportion}`)
    }

    let { coords } = scaledData;
    let [x, y] = coords[0]
    let path = new Path();
    path.draw(this._graph.element);
    path.moveTo(x, y);
    for (let i = 1, length = coords.length; i < length; i++) {
      let [x, y] = coords[i];
      path.lineTo(x, y);
    }

    path
      .className('line')
      .attr({stroke: data.color})
      .attr({transform: `scale(1, -1) translate(0, -${height})`})

    this._data[data.id].path = path;
    this._data[data.id].enabled = true;
    x1 = (x1 / xRatio) * scaledData.xRatio;
    this._graphTranslate = -x1;
    this._graphScale = 1;

    this._graph.attr({
      transform: `translate(${this._graphTranslate} 0)`
    })

    this._select.add(data, (enable) => {
      if (enable) {
        preview.enable(data.id);
        //this.enable(data.id)
      } else {
        preview.disable(data.id);
        //this.disable(data.id)
      }
    });
  }

  /**
   * TODO: Not implemented
   */
  _drawData(dataId) {
    let data = this._data[dataId]
    let scaledData = data.scaledData;

    //this._grid.updateTitles(null, null, data.maxYValue);

    if (!this._graph) {
      this._graph = new G();
      this._graph
        .draw(this._svg.element)
    }

    let height = this._getPlotHeight();
    let width = height * data.proportion

    let { coords } = scaledData;
    let [x, y] = coords[0]
    let path = new Path();
    path.draw(this._graph.element);
    path.moveTo(x, y);
    for (let i = 1, length = coords.length; i < length; i++) {
      let [x, y] = coords[i];
      path.lineTo(x, y);
    }

    path
      .className('line')
      .attr({stroke: data.data.color})
      .attr({transform: `scale(1, -1) translate(0, -${height})`})

    this._data[dataId].path = path;
    this._data[dataId].enabled = true;

    let x1 = (this._graphX1 / data.xRatio) * scaledData.xRatio;
    this._graphTranslate = -x1;
    this._graphScale = this._graphScale || 1;

    this._graph.attr({
      transform: `translate(${this._graphTranslate} 0)`
    })
  }

  _getPlotWidth() {
    return this._svg.element.getBoundingClientRect().width;
  }

  _getPlotHeight() {
    let height = this._svg.element.getBoundingClientRect().height;
    return height - yPadding;
  }

  _bindUI() {
    this._svg.element.addEventListener('mousemove', (e) => {
      let x = e.offsetX
      this._selectCoord(x);
    });

    this._svg.element.addEventListener('mouseleave', (e) => {
      //TODO: refactor
      this._graph.__path.element.remove();
      this._graph.__path = null;
      _.forIn(this._data, ({path}) => {
        path.__highlight.element.remove();
        path.__highlight = null;
      });
    })
  }

  _showPopup(timestamp, x, info) {
    let title = new Date(timestamp).toDateString();
    x = this._svg.element.parentNode.offsetTop + 10;

    this._popup.show(x, null, title, info);
  }

  _selectCoord(x) {
    let pos = (x + Math.abs(this._graphTranslate)) / this._graphScale;
    let timestamp;

    let info = []
    _.forIn(this._data, ({ data, scaledData, path }) => {
      timestamp = data.minXValue + (pos / scaledData.xRatio);
      let closest = this._selectPath(data, scaledData, path, timestamp);

      info.push({
        name: data.name,
        value: closest[1],
        color: data.color
      })
    })

    this._showPopup(timestamp, x, info);
  }

  /**
   * TODO: improve speed
   */
  _selectPath(data, scaledData, path, timestamp) {
    let values = data.values;
    let i = 0;
    for (i = 1, length = values.length; i < length; i++) {
      if (values[i][0] > timestamp) {
        break;
      }
    }
    let start = i > 1 ? values[i - 1] : values[0];
    let end = i == values.length ? values[i - 1] : values[i]
    let closest = (end[0] - timestamp) > (timestamp - start[0]) ? start : end;

    let height = this._getPlotHeight()
    let x = (closest[0] - data.minXValue) * scaledData.xRatio;
    let y = height - (closest[1] * scaledData.yRatio);

    this._highlightPath(path, x, y);
    return closest;
  }

  //TODO: refactor
  _highlightPath(path, x, y) {

    if (path.__highlight) {
      let circle = path.__highlight;
      circle
        .attr({
          cx: x,
          cy: y,
          r: 4,
          stroke: path.attr('stroke'),
        })
        .className('highlight')

      {
        let path = this._graph.__path;
        path
          .attr({'d': `M ${x} 0`})
          .lineTo(x, this._getPlotHeight());
      }
      return;
    }

    let circle = new Circle();
    circle.draw(this._graph.element);
    path.__highlight = circle;
    if (!this._graph.__path) {
      let path = new Path()
      path.draw(this._graph.element);
      path.moveTo(x, 0)
        .lineTo(x, this._getPlotHeight())
        .className('highlight_line')
      this._graph.__path = path;
    }
    this._highlightPath(path, x, y);
  }
}

export default Plot
