const Utils = {
  // https://github.com/lodash/lodash/blob/750067f42d3aa5f927604ece2c6df0ff2b2e9d72/isObject.js
  isObject: (value) => {
    const type = typeof value;
    return value != null && (type == 'object' || type == 'function');
  },

  isString: (value) => {
    return typeof value == 'string'
  },

  isArray: (arr) => {
    return Array.isArray(arr);
  },

  filter: (a, cb) => {
    return Array.prototype.filter.call(a, c);
  },

  forIn: (obj, cb) => {
    if (!cb) return;

    for (let k in obj) {
      cb(obj[k], k);
    }
  },

  first: (obj) => {
    for (let i in obj) {
      return obj[i];
    }
  },

  ceil(val, acc) {
    if (acc) {
      let zeros = Math.pow(10, acc);
      return Math.ceil(val * zeros) / zeros;
    } else {
      return Math.ceil(val);
    }
  },

  forEach: (arr, cb) => {
    if (!cb || !arr) return;

    for (let i = 0, length = arr.length; i < length; i++) {
      cb(arr[i], i);
    }
  },

  closestPath() {

  }
}

export default Utils
