import { G, Path, Text } from '../../svg/index'
import _ from '../../services/utils'

const yAxisCount = 6;
const xAxisCount = 0;
const yPadding = 20;

class Grid {
  constructor(svg) {
    this._svg = svg;

    this._lines = this._drawAxis();
  }

  updateTitles(minXValue, maxXValue, maxYValue) {

    // TODO: normalize
    let yStep = _.ceil(maxYValue / yAxisCount);
    _.forEach(this._lines, (v, i) => {
      v.title.text(i * yStep);
    })

    let xStep = (maxXValue - minXValue) / xAxisCount;
    _.forEach(this._lines[0].points, (point, i) => {
      // TODO: dependency on data, move out
      let timestamp = i * xStep

      point.text(timestamp)
    });
  }

  get element() {
    return this._g.element;
  }

  clean() {
  }

  _drawAxis() {
    let g = new G();
    this._g = g;
    g.draw(this._svg.element);
    let axises = [];

    for (let i = 0; i < yAxisCount; i++) {
      let axis = this._drawXAxis(g.element, i);
      axises.push(axis);
    }

    return axises;
  }

  _drawXAxis(parentElement, index) {
    let height = this._getHeight();
    let width = this._getWidth();

    let y = height - ((height - yPadding) / yAxisCount) * (index)

    let g = new G();
    g.draw(parentElement)
      .attr({
        transform: `translate(0, ${y})`
      })
      .className('axis x_axis');

    let xAxis = new Path();
    xAxis
      .attr({
        d: `M 0 0 L ${width} 0`,
      })

    g.append(xAxis);

    let title = new Text();
    title
      .draw(g.element)
      .attr({
        y: -2
      })

    if (index != 0) {
      return {
        title: title,
        g: g
      }
    }

    // draw based axis
    let points = []
    for (let i = 0; i < xAxisCount; i++) {
      let x = (width / xAxisCount) * (i + 1)
      let title = new Text();
      title
        .draw(g.element)
        .attr({
          x: x,
          y: 0
        })
      if (i == (xAxisCount - 1)) {
        title.attr({'text-anchor': 'end'});
      }
      points.push(title);
    }

    return {
      title: title,
      g: g,
      points: points
    }
  }

  _getWidth() {
    return this._svg.element.getBoundingClientRect().width;
  }

  _getHeight(scale) {
    return this._svg.element.getBoundingClientRect().height - yPadding;
  }

}

export default Grid
