import _ from '../services/utils'
import Logger from '../services/Logger'

class BaseSVG {
  static NS = "http://www.w3.org/2000/svg"
  static XLINK_NS = "http://www.w3.org/1999/xlink"

  constructor(DOM) {
    const c = this.constructor;
    if (c.id === undefined) {
      c.id = 0;
    }
    c.id++;

    let name = c.tagName ? `<${c.tagName}/>` : c.name;
    this.console = new Logger(`${name} #${c.id}`, c.LOG_LEVEL);
    this._DOM = DOM
  }

  /**
   * @param {Object|String} params
   * @return {Object|any} this|value
   */
  attr(params) {
    if (_.isObject(params)) {
      _.forIn(params, (v, k) => {
        this._element.setAttribute(k, v);
      })
      return this;
    }

    if (_.isString(params)) {
      return this._element.getAttribute(params)
    }

    return this;
  }

  draw(parentElement, opts, config) {
    return this.__draw(parentElement, opts, config);
  }

  __draw(parentElement, opts, config) {
    const c = this.constructor;
    this._element = this._DOM.createElementNS(c.NS, c.tagName);
    this._parentElement = parentElement;

    parentElement.append(this._element)
    return this;
  }
  /**
   * @param {String} str
   */
  className(str) {
    return this.attr({'class': str});
  }

  /**
   * @param {BaseSVG} svg
   */
  add(svg) {
    this._element.parentNode.appendChild(svg.element);
  }

  /**
   * @param {BaseSVG} svg
   */
  append(svg) {
    this._element.appendChild(svg.element);
  }

  clean() {
    let childNodes = this._element.childNodes;
    let element = this._element;
    while (childNodes.length) {
      element.removeChild(childNodes[0]);
    }
  }

  get element() { return this._element;  }
}

export default BaseSVG
