import Logger from '../../services/Logger'
import BaseSVG from '../../svg/base'
import _ from '../../services/utils'

import { G, Path, SVG } from  '../../svg/index'

import html from './index.html'
import styles from './styles.css'

import Slider from './Slider'

class Preview extends BaseSVG {
  static LOG_LEVEL = 10

  /**
   * @param {DOMElement} parentNode
   * @param {Object} config
   *  @key {Object} slider
   *    @key {Number} sliderX, default=100
   *    @key {Number} sliderWidth, default=300
   */
  constructor(parentNode, config = {}) {
		super()

    this._parentNode = parentNode;
    this._data = {};

    let doc = parentNode.ownerDocument.createElement('div');
    doc.innerHTML = html;
    let currentNode = doc.firstChild;
    this._parentNode.append.apply(this._parentNode, doc.childNodes);

    this._svg = null;
    this._g = null;

    this._x1 = null;
    this._x2 = null;
    this._slider = new Slider(currentNode, config.slider)
    let [x1, x2] = this._slider.getVisibleRange();

    this._x1 = x1;
    this._x2 = x2;
    this._slider.subscribe('onChange', ([x1, x2]) => {
      this._x1 = x1;
      this._x2 = x2;

      let data = _.first(this._data).scaledData;
      this.emit('onChange', data, [this._x1, this._x2], [data.xRatio, data.yRatio]);
    });

    this._slider.subscribe('onScale', ([x1, x2]) => {
      this._x1 = x1;
      this._x2 = x2;

      let data = _.first(this._data).scaledData;
      this.emit('onScale', data, [this._x1, this._x2], [data.xRatio, data.yRatio]);
    });
  }

  /**
   * PubSub mixin
   */
  subscribe(eventType, cb) {
    if (!this.__subs)
      this.__subs = {};

    let subs = this.__subs[eventType] || [];
    subs.push(cb);

    this.__subs[eventType] = subs;
  }

  emit(eventType) {
    if (!this.__subs)
      this.__subs = {};

    let subs = this.__subs[eventType];
    let args = Array.prototype.slice.call(arguments, 1);
    _.forEach(subs, sub => {
      sub.apply(null, args);
    });
  }

  /**
   * Remove all nodes from svg
   */
  clean() {
    this._g.clean();
    this._data = {}
  }

  enable(id) {
    let data = this._data[id];
    if (!data)
      return;

    if (data.enabled)
      return;

    this._drawPath(id);
  }

  disable(id) {
    let data = this._data[id];
    if (!data)
      return;

    if (!data.enabled)
      return;

    data.path.element.remove();
    data.path = null;
    data.enabled = false;
  }

  /**
   * @param {GraphData} data
   * @param {Object} opts
   *  @key {Number} xPadding
   *  @key {Number} yPadding
   * @param {Object} config
   *  @key {String} svgId
   */
  draw(data = null, opts = {}, config = {}) {
    if (!this._svg) {
      this._svg = new SVG()
      let previewNode = this._parentNode.querySelector('.preview');
      this._svg.draw(previewNode, {fillIn: true});
    }

    if (!this._g) {
      this._g = new G();
      this._g.draw(this._svg.element, null, {id: config.svgId});
    }

    if (data)
      this.drawOn(data, opts, config);
  }

  /**
   * @param {GraphData} data
   * @opts {Object} opts
   *  @key {Number} xPadding
   *  @key {Number} yPadding
   */
  drawOn(data, opts = {}, config = {}) {
    const xPadding = opts.xPadding || 0;
    const yPadding = opts.yPadding || 0;
    this._data[data.id] = {data: data}
    let scaledData = this._data[data.id].scaledData = data.scaleTo(this._svg.width - xPadding * 2, this._svg.height - yPadding * 2);
    //TODO: not good
    this._data[data.id].xPadding = xPadding;
    this._data[data.id].yPadding = yPadding;

    this._drawPath(data.id);
    this.emit('onDraw', data,
      [this._x1, this._x2],
      [scaledData.xRatio, scaledData.yRatio],
      scaledData.proportion);

    //hack
    this.emit('onScale', scaledData, [this._x1, this._x2], [scaledData.xRatio, scaledData.yRatio]);
  }

  _drawPath(dataId) {
    let { scaledData, xPadding, yPadding, data } = this._data[dataId]
    let { coords } = scaledData;

    let path = new Path();
    let [x, y] =  coords[0];
    path.moveTo(x + xPadding, y + yPadding);

    for (let i = 1, length = coords.length; i < length; i++) {
      let [x, y] = coords[i];
      path.lineTo(x + xPadding, y + yPadding);
    }
    path
      .className('preview_line')
      .attr({stroke: data.color})
      .attr({transform: `scale(1, -1) translate(0, -${this._svg.height})`})

    this._data[dataId].path = path;
    this._data[dataId].enabled = true;
    this._g.append(path);
  }
}

export default Preview
