import _ from '../../services/utils'

const defaultConfig = {
  sliderX: 12,
  sliderWidth: 200,
  sliderMinWidth: 100
}

class Slider {
  /**
   * @param {DOMElement} parentNode
   * @key {Object} slider
   *  @key {Number} sliderX
   *  @key {Number} sliderWidth
   *  @key {Number} sliderMinWidth
   */
  constructor(parentNode, config = defaultConfig) {
    this._parentNode = parentNode;

    this.UI = {
      left: this._parentNode.querySelector('.slider_left'),
      overflowLeft: this._parentNode.querySelector('.overflow_left'),
      right: this._parentNode.querySelector('.slider_right'),
      overflowRight: this._parentNode.querySelector('.overflow_right'),
      mid: this._parentNode.querySelector('.slider_mid'),
    }

    this._initUI(config);
  }

  /**
   * @param {Array} coords
   *  @0 {Number} x1
   *  @1 {Number} x2
   */
  getVisibleRange() {
    const { width } = this.UI.mid.getBoundingClientRect();
    const left = this.UI.mid.offsetLeft;
    const leftWidth = this.UI.left.getBoundingClientRect().width;

    return [left - leftWidth, width + left - leftWidth];
  }

  /**
   * PubSub mixin
   */
  subscribe(eventType, cb) {
    if (!this._subs)
      this._subs = {};

    let subs = this._subs[eventType] || [];
    subs.push(cb);

    this._subs[eventType] = subs;
  }

  emit(eventType) {
    if (!this._subs)
      this._subs = {};

    let subs = this._subs[eventType];
    let args = Array.prototype.slice.call(arguments, 1);
    _.forEach(subs, sub => {
      sub.apply(null, args);
    });
  }


  _initUI(config = defaultConfig) {
    const { left, right, mid, overflowLeft, overflowRight } = this.UI;

    const width = mid.parentNode.getBoundingClientRect().width;
    const leftWidth = left.getBoundingClientRect().width;
    const rightWidth = left.getBoundingClientRect().width;

    mid.style.width = `${config.sliderWidth + leftWidth}px`;
    overflowLeft.style.width = `${config.sliderX}px`
    let overflowRightWidth = width - config.sliderWidth - config.sliderX - leftWidth;
    overflowRight.style.width = `${overflowRightWidth}px`

    this._bindUI(config);
  }

  _bindUI(config = defaultConfig) {
    const { left, right, mid, overflowLeft, overflowRight } = this.UI;

    const width = mid.parentNode.getBoundingClientRect().width;
    const leftWidth = left.getBoundingClientRect().width;
    const rightWidth = right.getBoundingClientRect().width;

    this._bindSlider(left, (e) => {
      let x = e.clientX - leftWidth;
      if (x < 0) x = 0;

      let overflowLeftWidth = x + leftWidth;
      let diff = overflowLeft.getBoundingClientRect().width - overflowLeftWidth;
      let midWidth = mid.getBoundingClientRect().width + diff;
      if (midWidth <= config.sliderMinWidth) {
        overflowLeftWidth += (midWidth - config.sliderMinWidth);
        midWidth = config.sliderMinWidth;
      }

      overflowLeft.style.width = `${overflowLeftWidth}px`;
      mid.style.width = `${midWidth}px`;

      this.emit('onScale', this.getVisibleRange());
    });

    this._bindSlider(right, (e) => {
      let diff = e.movementX;
      let prevMidWidth = mid.getBoundingClientRect().width;
      let midWidth =  prevMidWidth + diff;
      if (midWidth < config.sliderMinWidth) {
        diff = config.sliderMinWidth - prevMidWidth;
        midWidth = prevMidWidth + diff;
      }

      let overflowRightWidth = overflowRight.getBoundingClientRect().width - diff;
      if (overflowRightWidth < rightWidth) {
        midWidth += overflowRightWidth - rightWidth;
        overflowRightWidth = rightWidth;
      }

      overflowRight.style.width = `${overflowRightWidth}px`;
      mid.style.width = `${midWidth}px`;
      this.emit('onScale', this.getVisibleRange());
    });

    this._bindSlider(mid, (e) => {
      let diff = e.movementX;

      let overflowLeftWidth = overflowLeft.getBoundingClientRect().width + diff;
      if (overflowLeftWidth < leftWidth) {
        diff += leftWidth - overflowLeftWidth;
        overflowLeftWidth = leftWidth;
      }

      let overflowRightWidth = overflowRight.getBoundingClientRect().width - diff;
      if (overflowRightWidth < rightWidth) {
        diff -= rightWidth - overflowRightWidth;
        overflowRightWidth = rightWidth;
        overflowLeftWidth = overflowLeft.getBoundingClientRect().width + diff;
      }

      overflowLeft.style.width = `${overflowLeftWidth}px`;
      overflowRight.style.width = `${overflowRightWidth}px`;
      this.emit('onChange', this.getVisibleRange());
    });
  }

  _bindSlider(UI, cb) {
    let self = this;
    const doc = UI.ownerDocument;

    UI.addEventListener('mousedown', initDrag);
    function initDrag() {
      doc.addEventListener('mouseup', stopDrag)
      subscribeOnMove();
    }

    function stopDrag() {
      unsubscribeOnMove();
      doc.removeEventListener('mouseup', stopDrag);
    }

    function subscribeOnMove() {
      self._parentNode.addEventListener('mousemove', moveListener);
    }

    function unsubscribeOnMove() {
      self._parentNode.removeEventListener('mousemove', moveListener);
    }

    function moveListener(e) { cb(e); }
  }

}

export default Slider
