import Logger from '../services/Logger'
import BaseSVG from './base'

const DEFAULT_WIDTH = 500;
const DEFAULT_HEIGHT = 300;

class SVG extends BaseSVG {
  static NS = "http://www.w3.org/2000/svg"
  static id = 0


  constructor(DOM = window && window.document) {
    super();
    SVG.id++;

    this.console = new Logger(`SVG #${SVG.id}`)

    this._DOM = DOM
    this._element = null
    this._rootElement = null;
    this._width = null;
    this._height = null;
  }

  /**
   * @param {DOMElement} rootElement
   * @param {Object} opts
   *  @key {String|Number} width
   *  @key {String|Number} height
	 *  @key {Boolean} fillIn
   * @param {Object} config
   *  @key {String} id
   */
  draw(rootElement, opts = {}, config = {}) {
    this._element = this._DOM.createElementNS(SVG.NS, 'svg');
    this.attr({
      xmlns: SVG.NS,
      "xmlns:xlink": SVG.XLINK_NS,
      id: config.id || `svg-${SVG.id}`
    })
    this._rootElement = rootElement;

		this._width = this._getWidth(opts);
		this._height = this._getHeight(opts);
    console.log(this._width, opts);
    this._element.setAttribute('width',this._width);
    this._element.setAttribute('height', this._height);

    rootElement.append(this._element)

    return this;
  }

	_getWidth(opts) {
    if (opts.fillIn) {
      let { width } = this._rootElement.getBoundingClientRect();
      return width;
    }

    return opts.width || DEFAULT_WIDTH
	}
  
  _getHeight(opts) {
    if (opts.fillIn) {
      let { height } = this._rootElement.getBoundingClientRect();
      return height;
    }

    return opts.height || DEFAULT_HEIGHT
  }

  get width() {
    return this._width;
  }

  get height() {
    return this._height;
  }
}

export default SVG;
