import Logger from '../services/Logger'
import _ from '../services/utils'

import BaseSVG from './base'

class Path extends BaseSVG {
  static id = 0
  static tagName = 'path'

  constructor(doc = window && window.document) {
    super(doc)
    this.console = new Logger(`<path #${Path.id}/>`)
    Path.id++;

    this._element = doc.createElementNS(Path.NS, 'path');
  }

  moveTo (x, y) {
    let d = this.attr('d') || '';
    this.attr({d: `${d} M ${x} ${y}`});
    return this;
  }

  lineTo (x, y) {
    let d = this.attr('d') || '';
    this.attr({d: `${d} L ${x} ${y}`});
    return this;
  }
}

export default Path
