import BaseSVG from './base'

class Text extends BaseSVG {
  static LOG_LEVEL = 0;
  static tagName = 'text';

  constructor(DOM = window && window.document) {
    super(DOM);
  }

  text(value) {
    if (value == this._text)
      return;

    this._element.innerHTML = value;
    this._text = value;
  }
}

export default Text
