import SVG from './svg/SVG'
import Plot from './components/Plot/index'
import Preview from './components/Preview/index'
import _ from './services/utils'

import data from './data/chart_data.json'
import GraphData from './data/graph'

render(data[0], 'app1');

render(data[1], 'app2');

render(data[2], 'app3');

render(data[3], 'app4');

render(data[4], 'app5');

function render(data, id) {
  let inited = false;
  // TODO: move to components
  let svg = new SVG(window.document)
  let app = document.getElementById(id);
  svg = svg.draw(app, { width:  300, height: 200 });
  let plot = new Plot(svg);
  plot.draw();

  let preview = new Preview(app);

  let types = data.types;
  let lines = 0;
  for (let k in types) {
    if (types[k] == 'line') {
      lines++;
    }
  }

  let x = data.columns[0];
  if (x[0] !== 'x')
    throw new Error("First column doesn't contain x coords");

  let datas = []
  x = x.slice(1);
  for (let i = 0; i < lines; i++) {
    let y = data.columns[i + 1]
    let yName = y[0]
    y = y.slice(1);
    let color = data.colors[yName];
    let name = data.names[yName];
    let graphData = new GraphData(x, y, color, name);
    datas.push(graphData);
  }

  preview.draw(null, null);
  preview.clean();

  plot.clean();
  if (!inited) {
    plot.linkWith(preview);
    inited = true;
  }

  _.forEach(datas, graphData => {
    preview.drawOn(graphData, {xPadding: 12, yPadding: 5})
  })
}
