import _ from '../services/utils'
import Logger from '../services/Logger'

class GraphData {
  static id = 0

  /**
   * @param {Array} xValues
   * @param {Array} yValues
   */
  constructor(xValues, yValues, color, name) {
    GraphData.id++;
    this.id = GraphData.id;
    this.console = new Logger(`Graph #${this.id}`, 0)
    this._values = [];
    this._coords = [];
    this._minXValue = null;
    this._maxXValue = null;
    this._minYValue = 0;
    this._maxYValue = null;
    this._color = color;
    this._name = name;

    if (!_.isArray(xValues)) {
      this.console.error(`xValues should be array, but it's ${xValues}. Exit`);
      return;
    }

    if (!_.isArray(yValues)) {
      this.console.error(`yValues should be array, but it's ${yValues}. Exit`);
      return;
    }

    if (xValues.length != yValues.length)
      this.console.warn(`Array length mismatch x[length]=${xValues.length}, y[length] = ${yValues.length}`);

    _.forEach(xValues, (xValue, i) => {
      let yValue = yValues[i];

      if (this._minXValue === null || this._minXValue > xValue)
        this._minXValue = xValue;

      /*
      if (this._minYValue === null || this._minYValue > yValue)
        this._minYValue = yValue;
        */

      if (this._maxXValue === null || this._maxXValue < xValue)
        this._maxXValue = xValue;

      if (this._maxYValue === null || this._maxYValue < yValue)
        this._maxYValue = yValue;

      this._values.push([xValue, yValue]);
    })

    this._values = this._values.sort((a, b) => (a[0] - b[0]));
  }

  /**
   * @param {Number} width, in px
   * @param {Number} height, in px
   */
  scaleTo(width, height) {
    this.console.log(5, `Scale data to width=${width}px and height=${height}`);
    let xRange = this._maxXValue - this._minXValue;
    let yRange = this._maxYValue - this._minYValue;

    let xRatio = (width / xRange)
    let yRatio = (height / yRange)
    let coords = [];
    _.forEach(this._values, ([x, y]) => {
      coords.push([ (x - this._minXValue) * xRatio, (y - this._minYValue) * yRatio ]);
    })

    return {
      id: this.id,
      coords: coords,
      xRatio: xRatio,
      yRatio: yRatio,
      proportion: width / height
    }
  }

  get name() {
    return this._name;
  }

  get coords() {
    return this._coords;
  }

  get color() {
    return this._color;
  }

  get minXValue() {
    return this._minXValue;
  }
  get maxYValue() {
    return this._maxYValue;
  }

  get maxXValue() {
    return this._maxXValue;
  }

  get values() {
    return this._values;
  }

  [Symbol.iterator]() {
    let i = 0;
    let self = this;
    return {
      next() {
        i++;

        return {
          value: self._coords[i - 1],
          done: (i  + 1) == self._coords.length
        }
      }
    }
  }
}

export default GraphData
